/*  */
export function timeRule(date) {
	let moment = require('moment')
	// let date = '2019-08-15 09:48:05'  //
	let dateVal = moment(date).format('YYYY-MM-DD') //
	let currentDate = moment().format('YYYY-MM-DD') //
	let yearVal = moment(date).format('YYYY') //
	let currentYear = moment().format('YYYY') //
	let hourMinute = moment(date).format('HH:mm') //
	let monthDate = moment(date).format('MM-DD') //
	if (dateVal == currentDate) {
		//
		return hourMinute
	} else {
		//  ,  
		return yearVal == currentYear ? monthDate : dateVal
	}
}

/*  */
export function sendTimeRule(lastTime, newTime) {
	let moment = require('moment')
	let timeOut = 5 * 60 * 1000 //5
	// let lastTime = '2019-08-15 11:22:05'  //
	// let newTime = '2019-08-15 11:26:05'  //
	let sendStamp = moment(lastTime).valueOf() //
	let currentStamp = moment(newTime).valueOf() //
	let hourMinute = moment(newTime).format('HH:mm') //
	//5  ,
	return currentStamp - sendStamp > timeOut ? hourMinute : false
}

/* () */
export function computedNewMsgTime(last, next) {
	let moment = require('moment')
	let lastTime = moment(last)
	let nextTime = moment(next)
	if (nextTime.diff(lastTime, 'minute') >= 5) {
		return true
	} else {
		return false
	}
}

/*  */
export function msgTimeRule(date) {
	let moment = require('moment')
	let dateVal = moment(date).format('YYYY-MM-DD') //
	let currentDate = moment().format('YYYY-MM-DD') //
	let yearVal = moment(date).format('YYYY') //
	let currentYear = moment().format('YYYY') //
	let hourMinute = moment(date).format('HH:mm') //
	let monthDate = moment(date).format('MM-DD HH:mm') //
	let ymd = moment(date).format('YYYY-MM-DD HH:mm') //
	if (dateVal == currentDate) {
		//
		return hourMinute
	} else {
		//  ,  
		return yearVal == currentYear ? monthDate : ymd
	}
}

/*  */
export function grade(conut) {
	if (conut >= 0 && conut <= 54) {
		return {
			name: "",
			color: "#2ACF91"
		}
	} else if (conut >= 55 && conut <= 74) {
		return {
			name: "",
			color: "#FFA600"
		}
	} else if (conut >= 75 && conut <= 100) {
		return {
			name: "",
			color: "#FF5957"
		}
	}
}

/*  */
export function inquiry(type) {
	switch (type) {
		case "A": //
			return {
				name: "",
				color: "#DA58FF"
			}
		case "B": //
			return {
				name: "",
				color: "#3FC6FF"
			}
		case "C": //
			return {
				name: "",
				color: "#FE9846"
			}
		case "D": //
			return {
				name: "",
				color: "#5FDC28"
			}
		case "E": //
			return {
				name: "",
				color: "#0E92F8"
			}
	}
}
