import store from '@/store'
import { showModal, showToast } from '@/utils/commonJs'
import { imDisconnect } from '@/utils/imRong'

const PROD_SERVICE = '' //
// const DEV_SERVICE = 'http://www.nkzj999.com/wx/serve/test-doctor' //
const DEV_SERVICE = 'http://192.168.0.183:8502' //

function getSerive() { //
	if (process.env.NODE_ENV === 'production') {
		return PROD_SERVICE
	} else {
		return DEV_SERVICE
	}
}

function getSource() { //




	return "applet"

}

function showLoading(loading) { //loading
	if (loading) {
		uni.showLoading({
			title: '',
			mask: true
		})
	}
}

function hideLoding(loading) { //loading
	if (loading) {
		uni.hideLoading()
	}
}

function handleSuceesState(data, loading, toast) { //
	let datas = data.data
	let state = datas.code //
	return new Promise(resolve => {
		if (state != 200 && toast && data.statusCode == 200) {
			if (state == 901 || state == 902 || state == 903 || state == 702 || state == 703 || state == 706) { //
				store.commit("SET_TOKEN", '')
				store.commit("SET_INFO", '')
				showModal({
					content: datas.msg,
					showCancel: false,
					confirmText: "",
				}).then(() => {
					uni.redirectTo({
						url: "/pages/login/login"
					})
				})
			} else {
				showToast({
					title: datas.msg,
					icon: "none",
					duration: 2000
				})
			}
		}
		hideLoding(loading)
		resolve()
	})
}

export function getAction(url, data, loading = true, toast = true) { //GET
	showLoading(loading)
	return new Promise((resolve, reject) => {
		uni.request({
			// H5




			url: `${getSerive()}${url}`,

			data,
			method: "GET",
			header: {
				"X-Requested-With": "XMLHttpRequest",
				"token": store.state.token,
				"source": getSource()
			},
			success: res => {
				handleSuceesState(res, loading, toast).then(state => {
					resolve(res)
				})
			},
			fail: err => {
				reject(err)
				hideLoding(loading)
			}
		})
	})
}

export function postAction(url, data, loading = true, toast = true) { //POST
	showLoading(loading)
	return new Promise((resolve, reject) => {
		uni.request({
			// H5




			url: `${getSerive()}${url}`,

			data,
			method: "POST",
			header: {
				"X-Requested-With": "XMLHttpRequest",
				"Content-Type": "application/json",
				"token": store.state.token,
				"source": getSource()
			},
			success: res => {
				handleSuceesState(res, loading, toast).then(state => {
					resolve(res)
				})
			},
			fail: err => {
				reject(err)
				hideLoding(loading)
			}
		})
	})
}

export function fileAction(url, filePath, loading = true, toast = true) { //FILE
	showLoading(loading)
	return new Promise((resolve, reject) => {
		uni.uploadFile({
			// H5




			url: `${getSerive()}${url}`,

			filePath,
			name: 'file',
			formData: {
				'fileType': 'tx'
			},
			header: {
				"token": store.state.token,
				"source": getSource()
			},
			success: res => {
				res.data = JSON.parse(res.data) //json
				handleSuceesState(res, loading, toast).then(state => {
					resolve(res)
				})
			},
			fail: err => {
				reject(err)
				hideLoding(loading)
			}
		})
	})
}
