import store from '@/store'
import RongIMLib from './imWeb.js'
import { showModal } from '@/utils/commonJs'

const PROD_IM_APP_KEY = '8brlm7uf8z9j3' //IM
const DEV_IM_APP_KEY = '8luwapkv8jotl' //IM

let imRongInfo = {	//
	totalUnreadCount: 0,	//
	conversationList: [],	//
	takeMsg: {},	//
	takeUpdatedConversation: {},	//
}
let isStatus = false	//

/* IM */
export const im = RongIMLib.init({ appkey: getAppKey() }) 	

/* AppKey */
function getAppKey() {
	if (process.env.NODE_ENV === 'production') {
		return PROD_IM_APP_KEY
	} else {
		return DEV_IM_APP_KEY
	}
}

/* IM */
export function imLogin(imTokens) { 
	if (!imTokens) return
	imDisconnect()	//IM
	im.connect({
		token: imTokens
	}).then((res) => {
		console.log(',  id : ', res.id)
		imGetTotalUnreadCount()	//
		imGetConversationList()	// 
		imWatch()	//IM
	}).catch((error) => {
		console.log(': ', error.code, error.msg)
	})
}

/* IM */
export function imWatch() {
	im.watch({
	  conversation(event) { //
	    let updatedConversationList = event.updatedConversationList // 
			imRongInfo.takeUpdatedConversation = updatedConversationList[0]
			store.commit('SET_IM_RONG',imRongInfo)
			console.log(':',updatedConversationList)
	  },
	  message(event) {	//
	    let takeMsg = event.message
			imRongInfo.takeMsg = takeMsg
			imGetTotalUnreadCount()	//
			store.commit('SET_IM_RONG',imRongInfo)
	    console.log(':', takeMsg);
	  },
	  status(event) {	//
	    let status = event.status;
			switch (status) {
				case 0:
					console.log(':')
					imGetTotalUnreadCount()	//
					imGetConversationList()	// 
				break;
				case 1:
					console.log(':')
				break;
				case 2:
					console.log(':')
				break;
				case 3:
					console.log(':, SDK ')
				break;
				case 4:
					console.log(':Socket , SDK ')
				break;
				case 6:
					if (!isStatus) {
						console.log(':')
						isStatus = true
						store.commit("SET_TOKEN", '')
						store.commit("SET_INFO", '')
						imDisconnect()
						showModal({
							content: "~",
							showCancel: false,
							confirmText: "",
						}).then(() => {
							isStatus = false
							uni.redirectTo({
								url: "/pages/login/login"
							})
						})
					}
				break;
				case 12:
					console.log(':')
				break;
				default:
					console.log(`:${status}`)
				break;
			}
	  }
	})
}

